
import { getDJList } from "@/api/home";
export default {
  data() {
    return {
      list: [],
      id: this.$route.query.id,
    };
  },
  mounted() {
    this.getDJlist();
  
  },
  methods: {
    async getDJlist() {
      let result = await getDJList(this.id);
      console.log(result);
      if (result.data.code == 200) {
        this.list = result.data.programs;
      }
    },


    changeCount(num) {
      if (num >= 100000000) {
        return (num / 100000000).toFixed(1) + "亿";
      } else if (num >= 10000) {
        return (num / 10000).toFixed(1) + "万";
      }
    },
  },
};
