
import {getRecommendMusic} from '@/api/home'
export default {
    data() {
        return {
            playlist:{},
            cookie:JSON.parse(localStorage.getItem('cookies'))
        }
    },
    methods: {
        async getPlayList(){
            let result = await getRecommendMusic(this.cookie)
            console.log(result)
            if(result.data.code==200){
                this.playlist = result.data.recommend
            }
        }
    },
    mounted() {
        this.getPlayList()
    },
}
