
import {getMusiclist} from '@/api/home'
export default {
    data() {
        return {
            playlist:{},
            
        }
    },
    methods: {
        async getPlayList(){
            let result = await getMusiclist()
            console.log(result)
            if(result.data.code==200){
                this.playlist = result.data.result
            }
        }
    },
    mounted() {
        this.getPlayList()
    },
}
