
import { getpersonalFM } from "@/api/home";
export default {
  data() {
    return {
      list: [],
      cookie: JSON.parse(localStorage.getItem("cookies")),
    };
  },
  methods: {
    async getPersonalFM() {
      let result = await getpersonalFM(this.cookie);
      console.log(result);
      if (result.data.code == 200) {
        this.list = result.data.data;
      }
    },
  },
  mounted() {
    this.getPersonalFM();
  },
};
