
import { getSearch } from "@/api/home.js";
export default {
  data() {
    return {
      keyWordList: [],
      searchKey: "",
      searchList: [],
    };
  },
  mounted() {
    this.keyWordList = JSON.parse(localStorage.getItem("keyWordList")) || [];
  },
  methods: {
    async enterKey() {
      if (this.searchKey !== "") {
        this.keyWordList.unshift(this.searchKey);
        this.keyWordList = [...new Set(this.keyWordList)];
        if (this.keyWordList.length > 5) {
          this.keyWordList.splice(this.keyWordList.length - 1);
        }
        localStorage.setItem("keyWordList", JSON.stringify(this.keyWordList));
        let res = await getSearch(this.searchKey);
        console.log(res);
        this.searchList = res.data.result.songs;
        this.searchKey = "";
      }
    },
    delHistory() {
      localStorage.removeItem("keyWordList");
      this.keyWordList = [];
    },
    async searchHistory(item) {
      let res = await getSearch(item);
      console.log(res);
      this.searchList = res.data.result.songs;
    },
    updateIndex(item) {
      this.$store.commit("pushPlayList", item);
      this.$store.commit(
        "updatePlayListIndex",
        this.$store.state.playList.length - 1
      );
    },
  },
};
