
import { getTop } from "@/api/home";
export default {
  data() {
    return {
      Toplist: {},
      List: [],
    };
  },
  methods: {
    async getToplist() {
      let result = await getTop();
      console.log(result);
      if (result.data.code == 200) {
        this.Toplist = result.data.artistToplist;
        this.List = result.data.list;
      }
    },
    changeCount(num){
      if(num>=100000000){
        return (num/100000000).toFixed(1) + '亿'
      }else if(num>=10000){
        return (num/10000).toFixed(1) + '万'
      }
    }
  },
  mounted() {
    this.getToplist();
  },
};
