
import { getDJ } from "@/api/home";
export default {
  data() {
    return {
      list: [],
    };
  },
  mounted() {
    this.getDj();
  },
  methods: {
    async getDj() {
      let result = await getDJ();
      console.log(result);
      if (result.data.code == 200) {
        this.list = result.data.data;
      }
    },

    changeCount(num) {
      if (num >= 100000000) {
        return (num / 100000000).toFixed(1) + "亿";
      } else if (num >= 10000) {
        return (num / 10000).toFixed(1) + "万";
      }
    },
  },
};
